body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  scroll-behavior: smooth;
}


.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#navigation-left {
    transition: all 0.5s;
    position: fixed;
    top: 35%;
    padding: 10px;
    z-index: 1;
    border-radius: 6px;
    /* background-color: #000000a2; */
    text-align: center;
}

#navigation-left i {
    transition: all 0.2s;
    color: white;
    font-size: 2em;
    line-height: 1.8;
}

#navigation-left i:hover {
    text-shadow: 1px 1px 4px #c3c3c3, -1px -1px 4px #b7b7b7;
    transition: all 0.2s;
    

}

#navigation-left ul {
    align-items: center;
}

.hidden {
    left: -200px;
}

.fixed {
    left: 20px;
}


.top-albums-item {
    border-radius: 0px 0px 6px 0px;
    box-shadow: 0 2px 3px rgba(10,10,10,.1), 0 0 0 1px rgba(10,10,10,.1);
    margin-bottom: 12px;
    padding: 0px;
    width: 100%;
    overflow: hidden;
    transition: all 0.5s;
}

.top-albums-item:hover {
    background: #ff336057;
}


.top-albums-item-info {
    margin: 6px auto;
}

.top-albums-item-info-name {
    overflow: hidden;
    height: 36px;
}

.top-albums-item-cover-div {
    height: 72px;
    width: 72px;
    float:left;
    margin-right: 10px;
}

.top-albums-item-cover-img {
    height: 72px;
    width: 72px;
    border-bottom-right-radius: 50% 50%;
}

.top-albums-playcount {
    float:right;
    margin: 18px 18px 0px 0px;
    font-size: 1.4em;
}
.lds-ellipsis {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}
.lds-ellipsis div {
    position: absolute;
    top: 27px;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: rgba(0,0,0,.7);
    -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
            animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.lds-ellipsis div:nth-child(1) {
    left: 6px;
    -webkit-animation: lds-ellipsis1 0.6s infinite;
            animation: lds-ellipsis1 0.6s infinite;
}
.lds-ellipsis div:nth-child(2) {
    left: 6px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(3) {
    left: 26px;
    -webkit-animation: lds-ellipsis2 0.6s infinite;
            animation: lds-ellipsis2 0.6s infinite;
}
.lds-ellipsis div:nth-child(4) {
    left: 45px;
    -webkit-animation: lds-ellipsis3 0.6s infinite;
            animation: lds-ellipsis3 0.6s infinite;
}
@-webkit-keyframes lds-ellipsis1 {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@keyframes lds-ellipsis1 {
0% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
100% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
}
@-webkit-keyframes lds-ellipsis3 {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@keyframes lds-ellipsis3 {
0% {
    -webkit-transform: scale(1);
            transform: scale(1);
}
100% {
    -webkit-transform: scale(0);
            transform: scale(0);
}
}
@-webkit-keyframes lds-ellipsis2 {
0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
}
}
@keyframes lds-ellipsis2 {
0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
}
100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0);
}
}

